/**
 * Edge SDK - ‰∏ªÂÖ•Âè£
 * @module @chowbus/edge-sdk
 */

import Logger from './utils/logger.js';
import { detectNativeBridge, BridgeType } from './core/bridge-detector.js';
import HealthChecker from './core/health-checker.js';
import ConfigManager from './core/config-manager.js';
import { dispatchConfigChangeEvent, dispatchNativeBridgeErrorEvent } from './core/event-emitter.js';

const logger = new Logger('[EdgeSDK]');

/**
 * Edge SDK ‰∏ªÁ±ª
 */
class EdgeSDK {
  constructor() {
    this.initialized = false;
    this.configManager = new ConfigManager();
    this.healthChecker = null;
    this.nativeBridge = null;
    this.bridgeType = BridgeType.NONE;
  }

  /**
   * ÂàùÂßãÂåñ SDK
   * @param {Object} options ÈÖçÁΩÆÈÄâÈ°π
   * @param {string} options.localServerHealth - Local Server ÂÅ•Â∫∑Ê£ÄÊü• URL
   * @param {number} [options.healthCheckInterval=5000] - ÂÅ•Â∫∑Ê£ÄÊü•Èó¥ÈöîÔºàÊØ´ÁßíÔºâ
   * @param {number} [options.healthCheckTimeout=3000] - ÂÅ•Â∫∑Ê£ÄÊü•Ë∂ÖÊó∂ÔºàÊØ´ÁßíÔºâ
   * @param {string[]} options.whiteList - ÂüüÂêçÁôΩÂêçÂçï
   * @param {string[]} [options.blackList=[]] - ÂüüÂêçÈªëÂêçÂçï
   * @param {boolean} [options.enableLog=true] - ÊòØÂê¶ÂêØÁî®Êó•Âøó
   * @param {boolean} options.isLocalServerFirst - ÊòØÂê¶‰ºòÂÖà‰ΩøÁî® Local Server
   * @returns {Promise<void>}
   */
  async init(options = {}) {
    if (this.initialized) {
      logger.warn('SDK Â∑≤ÂàùÂßãÂåñÔºåË∑≥ËøáÈáçÂ§çÂàùÂßãÂåñ');
      return;
    }

    try {
      logger.log('üöÄ ÂàùÂßãÂåñ‰∏≠...');

      // 1. ËÆæÁΩÆÈÖçÁΩÆ
      this.configManager.setConfig(options);
      const config = this.configManager.getConfig();

      // 2. ËÆæÁΩÆÊó•ÂøóÂºÄÂÖ≥
      logger.setEnabled(config.enableLog);

      // 3. Ê£ÄÊµã Native Bridge
      this.nativeBridge = detectNativeBridge();
      if (this.nativeBridge) {
        this.bridgeType = this.nativeBridge.type;
        logger.success(`Native Bridge Â∑≤ËøûÊé•: ${this.bridgeType}`);
      } else {
        this.bridgeType = BridgeType.NONE;
        logger.warn('Native Bridge ‰∏çÂèØÁî®ÔºåËøêË°åÂú® Web Ê®°Âºè');
      }

      // 4. ÂàùÂßãÂåñÂÅ•Â∫∑Ê£ÄÊü•Âô®
      this.healthChecker = new HealthChecker(config);

      // 5. ÂêØÂä®ÂÅ•Â∫∑Ê£ÄÊü•
      this.healthChecker.start(async (isAlive) => {
        await this.handleHealthStatusChange(isAlive);
      });

      this.initialized = true;
      logger.success('‚úÖ ÂàùÂßãÂåñÂÆåÊàê');

    } catch (error) {
      logger.error('ÂàùÂßãÂåñÂ§±Ë¥•', error);
      throw error;
    }
  }

  /**
   * Â§ÑÁêÜÂÅ•Â∫∑Áä∂ÊÄÅÂèòÂåñ
   * @param {boolean} isAlive 
   * @private
   */
  async handleHealthStatusChange(isAlive) {
    const config = this.configManager.generateNativeConfig(isAlive);

    logger.log('üì§ Êõ¥Êñ∞ Native ÈÖçÁΩÆ', {
      isLocalAlive: isAlive,
      whiteListCount: config.whiteList.length,
      blackListCount: config.blackList.length
    });

    // ÈÄöÁü• Native
    if (this.nativeBridge) {
      try {
        await this.nativeBridge.setRouteConfig(config);
        logger.success('Native ÈÖçÁΩÆÂ∑≤Êõ¥Êñ∞');
      } catch (error) {
        logger.error('Native ÈÖçÁΩÆÊõ¥Êñ∞Â§±Ë¥•', error);
        dispatchNativeBridgeErrorEvent(error);
      }
    }

    // Ëß¶ÂèëËá™ÂÆö‰πâ‰∫ã‰ª∂
    dispatchConfigChangeEvent(isAlive, config);
  }

  /**
   * Ëé∑ÂèñÂΩìÂâçÁä∂ÊÄÅ
   * @returns {Object}
   */
  getStatus() {
    if (!this.initialized) {
      return {
        initialized: false,
        isLocalAlive: false,
        bridgeType: BridgeType.NONE,
        whiteList: [],
        blackList: [],
        isAllLocalServer: false
      };
    }

    const config = this.configManager.getConfig();
    const isLocalAlive = this.healthChecker.getStatus();

    return {
      initialized: true,
      isLocalAlive,
      bridgeType: this.bridgeType,
      whiteList: isLocalAlive ? config.whiteList : [],
      blackList: config.blackList,
      isAllLocalServer: isLocalAlive
    };
  }

  /**
   * ÊâãÂä®Ëß¶ÂèëÂÅ•Â∫∑Ê£ÄÊü•
   * @returns {Promise<boolean>} Local Server ÊòØÂê¶ÂèØÁî®
   */
  async manualCheck() {
    if (!this.initialized) {
      throw new Error('SDK Êú™ÂàùÂßãÂåñ');
    }

    logger.log('üîç ÊâãÂä®ÂÅ•Â∫∑Ê£ÄÊü•');
    const isAlive = await this.healthChecker.check();

    // Ëß¶ÂèëÁä∂ÊÄÅÊõ¥Êñ∞
    await this.handleHealthStatusChange(isAlive);

    return isAlive;
  }

  /**
   * Âä®ÊÄÅÊõ¥Êñ∞ÁôΩÂêçÂçï
   * @param {string[]} whiteList 
   */
  updateWhiteList(whiteList) {
    if (!this.initialized) {
      throw new Error('SDK Êú™ÂàùÂßãÂåñ');
    }

    this.configManager.updateWhiteList(whiteList);

    // Á´ãÂç≥Êõ¥Êñ∞ Native ÈÖçÁΩÆ
    const isAlive = this.healthChecker.getStatus();
    this.handleHealthStatusChange(isAlive);
  }

  /**
   * Âä®ÊÄÅÊõ¥Êñ∞ÈªëÂêçÂçï
   * @param {string[]} blackList 
   */
  updateBlackList(blackList) {
    if (!this.initialized) {
      throw new Error('SDK Êú™ÂàùÂßãÂåñ');
    }

    this.configManager.updateBlackList(blackList);

    // Á´ãÂç≥Êõ¥Êñ∞ Native ÈÖçÁΩÆ
    const isAlive = this.healthChecker.getStatus();
    this.handleHealthStatusChange(isAlive);
  }

  /**
   * ÈîÄÊØÅ SDK
   */
  destroy() {
    logger.log('üîÑ ÈîÄÊØÅ‰∏≠...');

    if (this.healthChecker) {
      this.healthChecker.stop();
      this.healthChecker = null;
    }

    this.initialized = false;
    this.nativeBridge = null;
    this.bridgeType = BridgeType.NONE;

    logger.success('‚úÖ Â∑≤ÈîÄÊØÅ');
  }
}

// ÂàõÂª∫Âçï‰æãÂÆû‰æã
const instance = new EdgeSDK();


instance.EdgeSDK = EdgeSDK;

export default instance;
