/**
 * ÈÖçÁΩÆÁÆ°ÁêÜÂô®
 */
import Logger from '../utils/logger.js';

const logger = new Logger('[ConfigManager]');

/**
 * ÈªòËÆ§ÈÖçÁΩÆ
 */
export const DEFAULT_CONFIG = {
  localServerHealth: '',
  healthCheckInterval: 5000,
  healthCheckTimeout: 3000,
  whiteList: [],
  blackList: [],
  enableLog: true
};

/**
 * ÈÖçÁΩÆÁÆ°ÁêÜÁ±ª
 */
class ConfigManager {
  constructor() {
    this.config = { ...DEFAULT_CONFIG };
  }

  /**
   * ËÆæÁΩÆÈÖçÁΩÆ
   * @param {Object} options 
   */
  setConfig(options = {}) {
    // È™åËØÅÂøÖÂ°´Â≠óÊÆµ
    if (!options.localServerHealth) {
      throw new Error('localServerHealth ÊòØÂøÖÂ°´ÂèÇÊï∞');
    }

    // È™åËØÅÁôΩÂêçÂçï
    if (!Array.isArray(options.whiteList)) {
      throw new Error('whiteList ÂøÖÈ°ªÊòØÈùûÁ©∫Êï∞ÁªÑ');
    }

    // ÂêàÂπ∂ÈÖçÁΩÆ
    this.config = {
      ...DEFAULT_CONFIG,
      ...options
    };

    // È™åËØÅÊï∞ÂÄºÁ±ªÂûã
    if (typeof this.config.healthCheckInterval !== 'number' || this.config.healthCheckInterval < 1000) {
      throw new Error('healthCheckInterval ÂøÖÈ°ªÊòØÂ§ß‰∫éÁ≠â‰∫é 1000 ÁöÑÊï∞Â≠ó');
    }

    if (typeof this.config.healthCheckTimeout !== 'number' || this.config.healthCheckTimeout < 500) {
      throw new Error('healthCheckTimeout ÂøÖÈ°ªÊòØÂ§ß‰∫éÁ≠â‰∫é 500 ÁöÑÊï∞Â≠ó');
    }

    logger.log('ÈÖçÁΩÆÂ∑≤Êõ¥Êñ∞', {
      localServerHealth: this.config.localServerHealth,
      healthCheckInterval: this.config.healthCheckInterval,
      healthCheckTimeout: this.config.healthCheckTimeout,
      whiteListCount: this.config.whiteList.length,
      blackListCount: this.config.blackList.length,
      isLocalServerFirst: this.config.isLocalServerFirst
    });
  }

  /**
   * Ëé∑ÂèñÈÖçÁΩÆ
   * @returns {Object}
   */
  getConfig() {
    return { ...this.config };
  }

  /**
   * Êõ¥Êñ∞ÁôΩÂêçÂçï
   * @param {string[]} whiteList 
   */
  updateWhiteList(whiteList) {
    if (!Array.isArray(whiteList)) {
      throw new Error('whiteList ÂøÖÈ°ªÊòØÊï∞ÁªÑ');
    }

    this.config.whiteList = whiteList;
    logger.log(`ÁôΩÂêçÂçïÂ∑≤Êõ¥Êñ∞ÔºåÊï∞Èáè: ${whiteList.length}`);
  }

  /**
   * Êõ¥Êñ∞ÈªëÂêçÂçï
   * @param {string[]} blackList 
   */
  updateBlackList(blackList) {
    if (!Array.isArray(blackList)) {
      throw new Error('blackList ÂøÖÈ°ªÊòØÊï∞ÁªÑ');
    }

    this.config.blackList = blackList;
    logger.log(`ÈªëÂêçÂçïÂ∑≤Êõ¥Êñ∞ÔºåÊï∞Èáè: ${blackList.length}`);
  }

  /**
   * ÁîüÊàê Native ÈÖçÁΩÆÂØπË±°
   * @param {boolean} isLocalAlive - Local Server ÊòØÂê¶ÂèØÁî®
   * @returns {Object} Native ÈÖçÁΩÆÂØπË±°
   * @returns {string[]} return.whiteList - ÁôΩÂêçÂçïÂàóË°®
   * @returns {string[]} return.blackList - ÈªëÂêçÂçïÂàóË°®
   * @returns {boolean} return.isAllLocalServer - ÊòØÂê¶ÂÖ®ÈÉ®Ëµ∞ Local Server
   * @returns {boolean} return.isLocalServerFirst - ÊòØÂê¶‰ºòÂÖàËµ∞ Local Server
   * @returns {boolean} return.isLocalServerEnabled - Local Server ÂøÉË∑≥ÊòØÂê¶ÊàêÂäü
   */
  generateNativeConfig(isLocalAlive) {
    return {
      whiteList: isLocalAlive ? this.config.whiteList : [],
      blackList: this.config.blackList,
      isAllLocalServer: isLocalAlive,
      isLocalServerFirst: this.config.isLocalServerFirst,
      isLocalServerEnabled: isLocalAlive
    };
  }
}

export default ConfigManager;