/**
 * ÂÅ•Â∫∑Ê£ÄÊü•Âô®
 */
import Logger from '../utils/logger.js';

const logger = new Logger('[HealthChecker]');

/**
 * ÂÅ•Â∫∑Ê£ÄÊü•Á±ª
 */
class HealthChecker {
  constructor(config) {
    this.config = config;
    this.isAlive = false;
    this.timer = null;
    this.checkInProgress = false;
  }

  /**
   * ÊâßË°åÂçïÊ¨°ÂÅ•Â∫∑Ê£ÄÊü•
   * @returns {Promise<boolean>} ÊòØÂê¶ÂèØÁî®
   */
  async check() {
    // Èò≤Ê≠¢Âπ∂ÂèëÊ£ÄÊü•
    if (this.checkInProgress) {
      logger.warn('ÂÅ•Â∫∑Ê£ÄÊü•Ê≠£Âú®ËøõË°å‰∏≠ÔºåË∑≥ËøáÊú¨Ê¨°Ê£ÄÊü•');
      return this.isAlive;
    }

    this.checkInProgress = true;

    try {
      const controller = new AbortController();
      const timeoutId = setTimeout(
        () => controller.abort(),
        this.config.healthCheckTimeout
      );

      const response = await fetch(this.config.localServerHealth, {
        method: 'GET',
        signal: controller.signal,
        cache: 'no-cache',
        headers: {
          'X-Health-Check': 'true'
        }
      });

      clearTimeout(timeoutId);

      const isAlive = response.ok;
      const statusChanged = this.isAlive !== isAlive;

      if (statusChanged) {
        const oldState = this.isAlive ? 'ALIVE' : 'DOWN';
        const newState = isAlive ? 'ALIVE' : 'DOWN';
        logger.log(`üîÑ Áä∂ÊÄÅÂèòÂåñ: ${oldState} ‚Üí ${newState}`);
      }

      this.isAlive = isAlive;
      return isAlive;

    } catch (error) {
      const wasAlive = this.isAlive;
      this.isAlive = false;

      if (wasAlive) {
        logger.warn('Local Server ‰∏çÂèØÁî®', error.message);
      }

      return false;

    } finally {
      this.checkInProgress = false;
    }
  }

  /**
   * ÂêØÂä®ÂÆöÊó∂Ê£ÄÊü•
   * @param {Function} onStatusChange Áä∂ÊÄÅÂèòÂåñÂõûË∞É
   */
  start(onStatusChange) {
    if (this.timer) {
      logger.warn('ÂÅ•Â∫∑Ê£ÄÊü•Â∑≤Âú®ËøêË°å‰∏≠');
      return;
    }

    logger.log(`ÂêØÂä®ÂÅ•Â∫∑Ê£ÄÊü•ÔºåÈó¥Èöî ${this.config.healthCheckInterval}ms`);

    // Á´ãÂç≥ÊâßË°å‰∏ÄÊ¨°
    this.check().then((isAlive) => {
      if (onStatusChange) {
        onStatusChange(isAlive);
      }
    });

    // ÂêØÂä®ÂÆöÊó∂Âô®
    this.timer = setInterval(async () => {
      const previousState = this.isAlive;
      const currentState = await this.check();

      // Áä∂ÊÄÅÂèòÂåñÊó∂Ëß¶ÂèëÂõûË∞É
      if (previousState !== currentState && onStatusChange) {
        onStatusChange(currentState);
      }
    }, this.config.healthCheckInterval);
  }

  /**
   * ÂÅúÊ≠¢ÂÆöÊó∂Ê£ÄÊü•
   */
  stop() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
      logger.log('ÂÅ•Â∫∑Ê£ÄÊü•Â∑≤ÂÅúÊ≠¢');
    }
  }

  /**
   * Ëé∑ÂèñÂΩìÂâçÁä∂ÊÄÅ
   * @returns {boolean}
   */
  getStatus() {
    return this.isAlive;
  }

  /**
   * ÈáçÁΩÆÁä∂ÊÄÅ
   */
  reset() {
    this.isAlive = false;
    this.checkInProgress = false;
  }
}

export default HealthChecker;